import { ExternalLink, Layers } from "lucide-react";

const projectData = [
  {
    title: "JOB-HIVE [cite: 48]",
    date: "Mar 2025 [cite: 48]",
    desc: "A subscription-based remote job searching platform with dynamic job filtering and an admin panel[cite: 51, 52].",
    tech: ["Next.js", "TypeScript", "Tailwind CSS", "MongoDB Atlas", "Razorpay [cite: 49]"],
  },
  {
    title: "BYTECODE [cite: 54]",
    date: "Jan 2025 [cite: 54]",
    desc: "Full-stack blog app with auth, CRUD functionality, and Cloudinary image uploads[cite: 56].",
    tech: ["Next.js 15", "MongoDB Atlas", "Cloudinary", "JavaScript", "Tailwind CSS [cite: 55]"],
  },
  {
    title: "DIZIT SOLUTION [cite: 58]",
    date: "Dec 2024 [cite: 58]",
    desc: "A service platform for booking AC repairs that directly notifies mechanics via WhatsApp[cite: 62].",
    tech: ["Next.js", "TypeScript", "Tailwind CSS", "WhatsApp API [cite: 59]"],
  }
];

export default function Projects() {
  return (
    <section className="py-20 border-t">
      <h3 className="text-3xl font-bold mb-12 flex items-center gap-3">
        <Layers className="text-blue-600" /> Key Projects
      </h3>
      <div className="grid md:grid-cols-2 gap-8">
        {projectData.map((project, i) => (
          <div key={i} className="p-8 bg-white border border-slate-200 rounded-3xl hover:border-blue-300 transition-all shadow-sm">
            <div className="flex justify-between items-start mb-4">
              <h4 className="text-2xl font-bold text-slate-800">{project.title}</h4>
              <span className="text-xs font-bold text-blue-500 uppercase tracking-widest">{project.date}</span>
            </div>
            <p className="text-slate-600 mb-6">{project.desc}</p>
            <div className="flex flex-wrap gap-2 mb-6">
              {project.tech.map((t) => (
                <span key={t} className="px-3 py-1 bg-slate-100 text-slate-500 text-[10px] font-bold rounded-md uppercase tracking-tight">
                  {t}
                </span>
              ))}
            </div>
            <button className="flex items-center gap-2 text-sm font-bold hover:text-blue-600 transition-colors uppercase tracking-wider">
              Live Link <ExternalLink size={14} />
            </button>
          </div>
        ))}
      </div>
    </section>
  );
}